import { Mark, Data } from '@ali/4ever-cangjie';

export interface SpacingData extends Data {
  value: number;
}

interface Spacing extends Mark<SpacingData> {}

const Spacing = {
  TYPE: 'spacing',

  create(value: number): Spacing {
    return Mark.create<SpacingData>({
      type: Spacing.TYPE,
      data: {
        value,
      },
    });
  },

  isSpacing(maybeSpacing: any): maybeSpacing is Spacing {
    return Mark.isMark(maybeSpacing) && maybeSpacing.type === Spacing.TYPE;
  },
};

export default Spacing;
